nav {
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 700;
  padding: 12px 20px 12px 18px;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
nav .NavWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}

nav .MainNavicon {
  fill: #fff;
  stroke: #fff;
  cursor: pointer;
}

nav .MainNavicon path {
  fill: #fff;
}

nav .MainNavicon button {
  cursor: pointer;
}

nav .Navbar-trigger {
  width: 27px;
  height: 29px;
  z-index: 2;
  transition: all 0.1s ease;
  margin-left: 2px;
}

nav .Navbar-trigger:hover svg {
  fill: #fff;
}

nav .Navbar-trigger button {
  padding: 4px;
  border: none;
  background-color: transparent;
  outline: none;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  margin: 0 auto;
}

nav .Navbar-trigger .triggerLines {
  display: block;
  background-color: #fff;
  width: 100%;
  height: 2.4px;
  -webkit-transform-origin: center;
  transform-origin: center;
}

nav .Navbar-logo {
  height: 28px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

nav .Navbar-logo a {
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-size: 23px;
  margin-bottom: auto;
  letter-spacing: 1px;
}

nav .Navbar-logo a svg {
  height: 100%;
  width: 90%;
}

nav .Navbar-right-icon {
  height: 31px;
  width: 33px;
}

nav .Navbar-right-icon a {
  padding: 5px 5px;
  display: block;
  width: 100%;
  height: 100%;
}

nav .Navbar-right-icon a svg {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

nav .Navbar-right-icon a svg .message-content {
  opacity: 0;
  transition: 0.1s ease;
}

nav .Navbar-right-icon a:hover svg .message-content {
  opacity: 0.8;
}

nav .fullHiddenNavbar,
nav .Navbar-preScreen {
  position: absolute;
}

@media screen and (min-width: 1100px) {
  nav {
    padding: 10px 18px 10px 18px;
  }
}

